<template lang="pug">
include ../../../mixins/bemto/bemto
+b.bpm-indicator
  +e.arrow(
    @mousedown="changeBpm('DECREASE')",
    @mouseup="stopBpmChange",
    @touchstart="changeBpm('DECREASE')",
    @touchend="stopBpmChange"
  )
    Icon(:iconClass="'icon-left2'")
  +e.info(:class="bpmStyle")
    +e.nums {{ song.bpm }}
    +e.text bpm
  +e.arrow(
    @mousedown="changeBpm('INCREASE')",
    @mouseup="stopBpmChange",
    @touchstart="changeBpm('INCREASE')",
    @touchend="stopBpmChange"
  )
    Icon(:iconClass="'icon-right2'")
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
export default {
  data: () => ({
    counter: false,
    bpmColorDebouncer: {},
    bpmStyle: '',
  }),
  watch: {
    'song.bpm': {
      handler() {
        this.bpmStyle = 'bpm-indicator__info--active'
        clearTimeout(this.bpmColorDebouncer)
        this.bpmColorDebouncer = setInterval(() => {
          this.bpmStyle = ''
        }, 200)
      },
    },
  },
  methods: {
    ...mapMutations(['DECREASE_CURRENT_BPM', 'INCREASE_CURRENT_BPM']),
    changeBpm(type) {
      if (!this.counter) {
        this.counter = setInterval(() => {
          this[`${type}_CURRENT_BPM`]()
        }, 80)
      }
    },
    stopBpmChange() {
      clearInterval(this.counter)
      this.counter = false
    },
  },
  computed: {
    ...mapGetters(['song']),
  },
}
</script>
